import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import { withCookies, Cookies } from 'react-cookie';
import { instanceOf } from 'prop-types';

import Home from './panels/Home';


class App extends React.Component {

	static propTypes = {
		cookies: instanceOf(Cookies).isRequired
	};


	constructor(props) {
		super(props);

		const { cookies } = props;
		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			token: cookies.get('userToken'),
			console: null,
			sR: null,
			loading: false,
			history: localStorage.getItem('history') || ''
		};
		console.log()
	}

	clearSR = () => {
	    this.setState({sR: null})
    };

	updateHistory = (vall) => {
		this.setState({history: this.state.history + ',' + vall});
		localStorage.setItem('history',this.state.history + ',' + vall, { path: '/' });
	};

	clearHistory = () => {
		this.setState({history: ''});
		localStorage.removeItem('history');
	};

	loading = () => {
		this.setState({loading: !this.state.loading})
	};

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppAccessTokenReceived':
					const { cookies } = this.props;
					cookies.set('userToken', e.detail.data.access_token, { path: '/' });

					this.setState({token: e.detail.data.access_token});
					break;
				case 'VKWebAppCallAPIMethodResult':
					this.setState({sR: e.detail.data.response.items});
					this.loading();
					break;
				default:
					this.setState({console: e.detail.type})
			}
		});

		if (!this.state.token) {
			connect.send("VKWebAppGetAuthToken", {"app_id": 6960579, "scope": "friends,video"})
		}

		connect.send('VKWebAppGetUserInfo', {});
	}




	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			this.state.fetchedUser ?
					<View activePanel={this.state.activePanel}>
						<Home id="home" data={this.state} go={this.go} connect={connect} loading={this.loading} updateHistory={this.updateHistory} clearSR={this.clearSR} clearHistory={this.clearHistory}/>
					</View>:
				<h1 align="center">Unsupported device!</h1>


		);
	}
}

export default withCookies(App);
