import React from 'react';
import { Panel, PanelHeader, Checkbox, Search, PanelHeaderContent, Cell, List, HeaderContext, Footer, Spinner, Tabs, TabsItem, FixedLayout,Button,Div } from '@vkontakte/vkui';
import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
import Icon16Add from '@vkontakte/icons/dist/16/add';

class Home extends React.Component {


	state = {
		adult: 0,
		q: '',
		contextOpened: false,
		hd: 0,
		longer: 0,
		count: 10,
		sort: 2
	};

	searchVideo = event => {
		if (event.key === 'Enter') {
			this.setState({count: 10});
			this.props.updateHistory(this.state.q);
			this.props.loading();
			this.props.connect.send("VKWebAppCallAPIMethod", {"method": "video.search", "params": {"sort": this.state.sort,"hd":this.state.hd,"longer":this.state.longer,"count":10,"q": this.state.q, "adult": this.state.adult, "v":"5.95", "access_token":this.props.data.token}});
		}
	};

	searchOffset = () => {
		this.props.loading();
		this.props.connect.send("VKWebAppCallAPIMethod", {"method": "video.search", "params": {"sort": this.state.sort,"hd":this.state.hd,"longer":this.state.longer,"count":this.state.count + 10,"q": this.state.q, "adult": this.state.adult, "v":"5.95", "access_token":this.props.data.token}});
		this.setState({count: this.state.count + 10});
	};


	searchVideoFromHistory = (vall) => {
		this.setState({q:vall});
		this.props.loading();
		this.props.connect.send("VKWebAppCallAPIMethod", {"method": "video.search", "params": {"sort": this.state.sort,"hd":this.state.hd,"longer":this.state.longer,"count":this.state.count,"q": vall, "adult": this.state.adult, "v":"5.95", "access_token":this.props.data.token}});

	};

	toggleContext = () => {
		this.setState({ contextOpened: !this.state.contextOpened });
	};

	searchSort = (vall) => {
		this.setState({count: 10, sort: vall});
		this.props.loading();
		this.props.connect.send("VKWebAppCallAPIMethod", {"method": "video.search", "params": {"sort": vall,"hd":this.state.hd,"longer":this.state.longer,"count": 10,"q": this.state.q, "adult": this.state.adult, "v":"5.95", "access_token":this.props.data.token}});
	};

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader>
					<PanelHeaderContent aside={<Icon16Dropdown />} onClick={this.toggleContext}>
						Видео
					</PanelHeaderContent>
				</PanelHeader>
				<HeaderContext opened={this.state.contextOpened} onClose={this.toggleContext}>
					<List>
						<Cell>
							<Checkbox onChange={()=>{this.setState({adult: 1 - this.state.adult%2})}} checked={!this.state.adult}>Безопасный поиск</Checkbox>
							<Checkbox onChange={()=>{this.setState({hd: 1 - this.state.hd%2})}} checked={this.state.hd}>Только высокое качество</Checkbox>
							<Checkbox onChange={()=>{this.setState({longer: this.state.longer === 0? 600: 0})}} checked={(this.state.longer - this.state.longer%600)/600}>Только длинные</Checkbox>
						</Cell>
					</List>
				</HeaderContext>
				<Search onChange={(value)=>{this.setState({q:value})}} value={this.state.q} onKeyPress={this.searchVideo} onClick={this.props.clearSR} autoComplete={true}/>

				{this.props.data.loading ? <Spinner style={{marginTop: 15, matginBottom: 15}}/>:null}
				<List>
				{this.props.data.sR ?
					this.props.data.sR.map((item,key)=>{
						const oid = item.owner_id;
						const id = item.id;
						return (
							<a href={`https://vk.com/video${oid}_${id}`} key={key}>
							<Cell >
								<table>
									<tbody>
										<tr>
											<td>
												<img src={
													item.photo_130
												} alt="pic" style={{width: 140}} />
											</td>
											<td style={{width:'70%'}} valign="top">
												<p style={{overflow: 'hidden',textOverflow: 'ellipsis', width: 240, marginTop: 0, marginBottom:4, textSize: 15}}>{item.title}</p>
												<p style={{overflow: 'hidden',textOverflow: 'ellipsis', width: 240, marginTop: 0, textSize: 11, color:'#939393'}}>{item.description}</p>
												<p style={{overflow: 'hidden',textOverflow: 'ellipsis', width: 240, marginTop: 0, textSize: 8, color:'#939393'}}>Просмотры: {item.views}</p>
											</td>
										</tr>
									</tbody>
								</table>
							</Cell>
								<hr style={{display: 'none'}}/>
							</a>
						)
					}):
				this.props.data.history? this.props.data.history.split(',').slice(1).reverse().map((item,key)=>(
					<Cell key={key} onClick={()=>{this.searchVideoFromHistory(item)}}>{item}</Cell>
				)) : null

				}

					{!this.props.data.sR && this.props.data.history ? <Div><Button align="center" size="xl" onClick={this.props.clearHistory}>Отчистить</Button></Div> : null}

					</List>
				{this.props.data.loading && this.props.data.sR ? <Spinner style={{marginTop: 15, matginBottom: 15}}/>:null}
				{ this.props.data.sR ? <Div>
					<Button align="center" size="xl" onClick={()=>{this.searchOffset()}} before={this.props.data.loading && this.props.data.sR ? <Spinner style={{marginTop: 15, matginBottom: 15}}/>:<Icon16Add/>}>Еще</Button>
				</Div>: null}
				<Footer>Поиск видео</Footer>
				{this.props.data.sR ?
					<FixedLayout vertical="bottom">
					<Tabs>
						<TabsItem selected={this.state.sort === 2} onClick={()=>{this.searchSort(2)}}>По релевантности</TabsItem>
						<TabsItem selected={this.state.sort === 0} onClick={()=>{this.searchSort(0)}}>По дате</TabsItem>
						<TabsItem selected={this.state.sort === 1} onClick={()=>{this.searchSort(1)}}>По длительности</TabsItem>
				</Tabs></FixedLayout>:null}
			</Panel>
		)
	}
}

export default Home;
